@font-face {
  font-family: "Golos";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../fonts/Golostextregular.woff2") format("woff2"), url("../fonts/Golostextregular.woff") format("woff");
}
@font-face {
  font-family: "Golos";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../fonts/Golostextbold.woff2") format("woff2"), url("../fonts/Golostextbold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  min-width: 320px;
}

body {
  min-width: 320px;
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
  background-color: #ffffff;
  font-family: "Golos", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 24px;
  text-align: left;
  color: #353e61;
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 0;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.section-title {
  font-family: "Golos", "Arial", sans-serif;
  font-style: normal;
  font-weight: bold;
  font-size: 40px;
  line-height: 56px;
  color: #353e61;
}

.btn {
  border: 1px solid transparent;
  vertical-align: middle;
}

.page-header {
  width: 100%;
  position: sticky;
  z-index: 1000;
  top: 0;
  background-color: #ffffff;
  box-shadow: 0px 3px 7px rgba(0, 0, 0, 0.3);
}

.page-header__wrapper {
  width: 100%;
  max-width: 1364px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 10px;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
}

.page-header__logo {
  width: 202px;
  height: 46px;
  margin-right: 370px;
}

.page-header__heart {
  margin-top: -20px;
}

.my-region {
  display: flex;
  flex-direction: row;
  width: 165px;
  margin-top: -20px;
}

.my-region__wrapper {
  position: relative;
  width: 55px;
  height: 25px;
}

.my-region__custom-checkbox {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: #000;
  border-radius: 30px;
  transition: 0.5s;
}

.my-region__checkbox {
  position: relative;
  z-index: 1;
  appearance: none;
}
.my-region__checkbox::before {
  content: "off";
  position: absolute;
  font-weight: bold;
  font-size: 24px;
  line-height: 28px;
  color: #000;
  bottom: -35px;
  left: 10px;
  cursor: pointer;
}

.my-region__checkbox:checked::before {
  content: "on";
  color: #e7401d;
}

.my-region__checkbox:checked ~ .my-region__custom-checkbox {
  background-color: #e7401d;
}

.my-region__label {
  cursor: pointer;
}

.my-region__custom-checkbox i {
  position: absolute;
  top: 1px;
  left: 1px;
  width: 23px;
  height: 23px;
  background-color: #e7401d;
  border-radius: 50%;
  transition: 0.5s;
}

.my-region__checkbox:checked ~ .my-region__custom-checkbox i {
  left: 31px;
  background-color: #000;
}

.my-region__text {
  display: block;
  position: absolute;
  top: 2px;
  left: 62px;
  width: 100px;
}

.main-nav {
  margin-top: -20px;
}

.main-nav__list {
  list-style: none;
  display: flex;
  flex-direction: row;
  padding-left: 0;
}

.main-nav__item {
  margin: 0 18px;
}

.main-nav__link {
  display: inline-block;
  color: #e7401d;
}
.main-nav__link:focus, .main-nav__link:hover {
  color: #00a9bf;
}
.main-nav__link:active {
  opacity: 0.5;
}

.languages-nav {
  display: flex;
  flex-direction: row;
  margin-top: -20px;
}

.languages-nav__link {
  display: inline-block;
  color: #e7401d;
  margin-left: 10px;
}
.languages-nav__link:focus, .languages-nav__link:hover {
  color: #00a9bf;
}
.languages-nav__link:active {
  opacity: 0.5;
}
.languages-nav__link--active {
  color: #353e61;
}
.languages-nav__link--active:focus, .languages-nav__link--active:hover, .languages-nav__link--active:active {
  color: #353e61;
  opacity: 1;
}

.promo-block {
  background-image: url("../img/header-big-circle.svg"), url("../img/header-rectangle-blue.svg");
  background-repeat: no-repeat;
  background-position: right -187px top -299px, left top 50px;
  background-size: 47% auto, 49% auto;
  position: relative;
  overflow: hidden;
}
.promo-block::before {
  content: "";
  position: absolute;
  width: 542px;
  height: 156px;
  bottom: 88px;
  left: -95px;
  background-image: url("../img/header-green-decor.svg");
  background-repeat: no-repeat;
  background-size: 542px 156px;
  z-index: 100;
}
.promo-block::after {
  content: "";
  position: absolute;
  width: 752px;
  height: 254px;
  bottom: 68px;
  right: -60px;
  background-image: url("../img/header-yellow-decor.svg");
  background-repeat: no-repeat;
  background-size: 752px 254px;
}

.promo-block__wrapper {
  width: 1364px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: flex-start;
  padding-top: 26px;
  padding-bottom: 140px;
  position: relative;
  background-image: url("../img/foot-pink.svg"), url("../img/foot-pink.svg");
  background-repeat: no-repeat;
  background-position: bottom 40px right 634px, top 15px left 760px;
  background-size: 86px auto, 68px auto;
}
.promo-block__wrapper::before {
  content: "";
  position: absolute;
  width: 160px;
  height: 160px;
  top: -32px;
  left: -210px;
  background-image: url("../img/header-red-circle.svg");
  background-repeat: no-repeat;
  background-size: 160px 160px;
}

.promo-block__info {
  color: #ffffff;
  margin-top: 26px;
  margin-right: 95px;
  padding: 68px 190px 50px 32px;
  border-radius: 0 275px 275px 0;
}

.promo-block__stock {
  width: 446px;
