  line-height: 32px;
  margin-bottom: 14px;
}

.promo-block__title {
  width: 420px;
  font-weight: bold;
  font-size: 46px;
  line-height: 48px;
  color: #57bde6;
  background-color: #ffffff;
  margin: 0;
  padding: 20px 20px 26px 30px;
  border-radius: 0 64px 64px 0;
