}

.promo-block__text {
  font-size: 18px;
  line-height: 28px;
  color: #ffffff;
  margin-bottom: 54px;
}

.promo-block__location {
  margin-bottom: 36px;
}

.promo-block__date {
  font-weight: bold;
  font-size: 24px;
  line-height: 36px;
  margin-bottom: 10px;
}

.promo-block__time,
.promo-block__address {
  padding-left: 30px;
  position: relative;
}
.promo-block__time::before,
.promo-block__address::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  top: -4px;
  left: 0px;
  background-repeat: no-repeat;
  background-size: 24px 24px;
}

.promo-block__time {
  margin-bottom: 10px;
}
.promo-block__time::before {
  background-image: url("../img/icon-calendar.svg");
}

.promo-block__address::before {
  background-image: url("../img/icon-location.svg");
}

.promo-block__picture {
  width: 600px;
  height: 602px;
  position: relative;
}
.promo-block__picture::before {
  content: "Счастье стать другом!";
  position: absolute;
  top: 146px;
  left: 17px;
  font-weight: bold;
  font-size: 18px;
  color: #ffffff;
  z-index: 100;
}
.promo-block__picture::after {
  content: "";
  position: absolute;
  width: 245px;
  height: 39px;
  top: 139px;
  left: -8px;
  background-color: #e7401d;
  border-radius: 32px;
  z-index: 99;
}

.promo-block__img {
  display: block;
  width: 600px;
  height: auto;
}

