.timetable__wrapper {
  width: 1290px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.timetable__label-list {
  width: 820px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  list-style: none;
  margin: 0 auto;
  margin-top: 6px;
  margin-bottom: 58px;
  padding-left: 0;
}

.timetable__label-item {
  font-weight: bold;
  line-height: 20px;
  color: #00a9bf;
  text-align: center;
}

.timetable__label-item label {
  display: flex;
  height: 100%;
  padding: 14px 40px;
  border: 1px solid #00a9bf;
